import React, { Component } from 'react';
import { compose } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';
import { withStyles } from 'material-ui/styles';
import AppBar from 'material-ui/AppBar';
import Toolbar from 'material-ui/Toolbar';
import Typography from 'material-ui/Typography';
import IconButton from 'material-ui/IconButton';
import { Link } from 'react-router-dom';
import Menu, { MenuItem } from 'material-ui/Menu';
import { ListItemIcon, ListItemText } from 'material-ui/List';
import LogoutIcon from '@material-ui/icons/Input';
import Avatar from '@material-ui/core/Avatar';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';

import { logout } from '../../../services/users/actions';

const styles = (theme) => ({
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    position: 'fixed',
  },
  flex: {
    flex: 1,
  },
});

class _AppBar extends Component {
  state = {
    anchorEl: null,
    dialogOpen: false,
  };

  handleLogout = () => {
    this.setState((prevState) => ({
      dialogOpen: !prevState.dialogOpen,
    }));
    this.handleMenuClose();
    this.props.logout();
  };

  handleMenu = (event) => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleMenuClose = () => {
    this.setState({ anchorEl: null });
  };

  handleToggleDialog = () => {
    this.setState((prevState) => ({
      dialogOpen: !prevState.dialogOpen,
    }));
  };

  render() {
    const { classes, user } = this.props;
    const { anchorEl } = this.state;
    const open = !!anchorEl;

    return (
      <AppBar
        position='absolute'
        className={[classes.appBar, 'appBar'].join(' ')}
      >
        <Toolbar>
          <Typography
            variant='title'
            color='inherit'
            className={classes.flex}
            noWrap
          >
            <Link to='/' style={{ textDecoration: 'none', color: 'unset' }}>
              Flip
            </Link>
          </Typography>
          <div>
            {!user.auth && (
              <Link
                to='/login'
                style={{ textDecoration: 'none', color: 'unset' }}
              >
                Login
              </Link>
            )}
            {user.auth && (
              <IconButton
                aria-owns={open ? 'menu-appbar' : null}
                aria-haspopup='true'
                onClick={this.handleMenu}
                color='inherit'
                src=''
              >
                <Avatar alt='Remy Sharp' src={user.profile} />
              </IconButton>
            )}
            <Menu
              id='menu-appbar'
              onClose={this.handleMenuClose}
              anchorEl={anchorEl}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={open}
            >
              <MenuItem
                onClick={this.handleMenuClose}
                component={NavLink}
                to='/profile'
              >
                Profile
              </MenuItem>
              <MenuItem
                onClick={() => {
                  this.handleMenuClose();
                  this.handleToggleDialog();
                }}
              >
                <ListItemIcon>
                  <LogoutIcon />
                </ListItemIcon>
                <ListItemText primary='Exit' />
              </MenuItem>
            </Menu>
          </div>
        </Toolbar>
        <Dialog
          open={this.state.dialogOpen}
          onClose={this.handleToggleDialog}
          aria-labelledby='alert-dialog-title'
          aria-describedby='alert-dialog-description'
          disableEnforceFocus
        >
          <DialogTitle id='alert-dialog-title'>
            Are you sure you want to leave?
          </DialogTitle>
          <DialogContent>
            <DialogContentText id='alert-dialog-description'>
              Stay with us a little longer, we're so cool :)
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleToggleDialog} color='primary'>
              Cancel
            </Button>
            <Button onClick={this.handleLogout} color='primary' autoFocus>
              Leave anyway
            </Button>
          </DialogActions>
        </Dialog>
      </AppBar>
    );
  }
}

_AppBar.propTypes = {
  classes: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  user: state.user.data,
});

export default compose(
  withStyles(styles),
  connect(mapStateToProps, { logout })
)(_AppBar);



